@use "./assets/sass/abstracts/variables" as *;

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    scroll-behavior: smooth;
}

a {
    color: inherit;
    text-decoration: none;
}

body {
    background-color: $primary-color;
    color: $secondary-color;
    font-family: $primary-font;
}
